  padding: 0;
  width: auto;
  height: auto;
  border: none;
  margin: 0;
  left: 1%;
  right: 1%;
  bottom: 1%;
  top: 1%;
  -webkit-transition: .4s ease-in-out;
  transition: .4s ease-in-out;
}

.cateye.inner-2 {
  left: 25%;
  right: 25%;
  bottom: 25%;
  top: 25%;
  background: #000;
  -webkit-transition: .4s ease-in-out;
  transition: .4s ease-in-out;
  height:4vmin;
  width:4vmin;
}

