.cateye.inner {
  position: absolute;
  background: #FFCD66;
  padding: 0;
  width: auto;
  height: auto;
  border: none;
  margin: 0;
  left: 1%;
  right: 1%;
  bottom: 1%;
  top: 1%;
  transition: .4s ease-in-out;
}

.cateye.inner-2 {
  left: 25%;
  right: 25%;
  bottom: 25%;
  top: 25%;
  background: #000;
  transition: .4s ease-in-out;
  height:4vmin;
  width:4vmin;
}

.cat:hover .cateye.inner-2 {
  height:2vmin;
  width:2vmin;
  left: 40%; 
  top:40%;
  -webkit-animation: cateyes 6s ease-in-out 1;
  animation: cateyes 6s ease-in-out 1;
}

.cateyelid {
  position: absolute;
  height: 50%;
  left: 0;
  right: 0;
  background: #0E3D58;
  transition: margin 0.15s ease;
}

.cateyelid.top {
  display:block;
  top: 0;
  border-radius: 200px 200px 0 0;
}

.cateyelid.bottom {
    display: block;
    bottom: 0;
    border-radius: 0 0 200px 200px;
    margin-bottom: -50%;
    -webkit-animation: catblink 4s ease-in-out 2s infinite;
    animation: catblink 4s ease-in-out 2s infinite;
}

/* The animation code */

@keyframes catblink {
0% {margin-bottom: -50%;}
3% {margin-bottom: 0%;}
6% {margin-bottom: -50%;}
100% {margin-bottom: -50%;}
}

@-webkit-keyframes catblink {
0% {margin-bottom: -50%;}
3% {margin-bottom: 0%;}
6% {margin-bottom: -50%;}
100% {margin-bottom: -50%;}
}

